		 <el-upload
		   action="https://jsonplaceholder.typicode.com/posts/"
		   list-type="picture-card"
		   :before-upload="ww"
		   :on-preview="handlePictureCardPreview"
		   :on-change="mm"
		   :on-remove="handleRemove">
		   <i class="el-icon-plus"></i>
		 </el-upload>
		 <el-dialog :visible.sync="dialogVisible">
		   <img width="100%" :src="dialogImageUrl" alt="" class="picc">
		 </el-dialog>
		 
        </div>

    </div>
    <div class="ss">
        <mt-button type="primary" class="sure">确认提交</mt-button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'List',
    data(){
      return{
        value:"",
        len:0,
		buttons:["投诉","建议","表扬"],
		dialogImageUrl: 'url',
		dialogVisible: false
      }
    },

    watch:{
       "value":function(){
        this.len=this.value.length;
      },
	  "dialogImageUrl":function(){
	  	///console.log(this.dialogImageUrl);
	  }
	 
    },
	mounted(){
	
		$(".bt:first").toggleClass("bt1");	
	},

	methods: {
		btc:function(){
			$(".bt1").removeClass("bt1");
			$(event.target).toggleClass("bt1");
			
			
		},
		 handleRemove(file, fileList) {
		         console.log(file, fileList);
		      },
		      handlePictureCardPreview(file,fileList) {
		        this.dialogImageUrl = file.url;
		        this.dialogVisible = true;			
				 console.log(file,fileList)
		      },
			  ww(file){
				 
				  // console.log(file.name)
				  // console.log(file.size)
				  // console.log(file.url)
				  //  console.log(file)
				  
			  },
			  mm(file, fileList){
			  				 
			  				  console.log(file.name)
			  				  console.log(file.size)
			  				  console.log(file.url)
							  console.log(file)
			  				  console.log(fileList)
			  				  
			  },


	}


  }
</script>

<style scoped>
	.ppp /deep/ .el-upload__input{
		display: none;
	}
	.ppp /deep/ .el-upload--picture-card{
		width:70px;
		height:70px;
	}
	.ppp /deep/ .el-icon-plus{
		position: relative;
		top:-35px;
	}
	.ppp /deep/ .el-upload-list__item {
		width: 70px;
		height:70px;
	}
