	 display: none;
  }
  .icon2{
    width: 50px;
    height: 50px;
  }
  .len{
   float: right;

  }
.pic{
  width: 100%;
}
.c1{
 padding-left: 20px;
 padding-right: 20px;
}

  .dd{
