    color: gray;
    font-size: 15px;
  }
  .t2{
    padding-top: 5px;
    padding-bottom: 15px;
    border-bottom: 1px solid gainsboro;
  }
  .t3{
    margin-top: 10px;
    font-size: 15px;
    width: 100%;
    border: none;


  }
  .t4{
    margin-top: 10px;
  }

.sure{

  font-size: 13px;


}
.ss{
  margin-top: 40px;
  padding-left: 20px;
  padding-right: 20px;
}
</style>



