<template>
 <div id="app">

<mt-navbar v-model="selected">
  <mt-tab-item id="1" ><span class="title" @click="qk()">未读</span></mt-tab-item>
  <mt-tab-item id="2" ><span class="title"@click="qk()">已读</span></mt-tab-item>
</mt-navbar>


<mt-search v-model="value" class="search">
</mt-search>
		
      <div class="page-loadmore-wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-spinner v-show="list<1 && InitialLoading" color="#26a2ff" class="center"></mt-spinner>

  <mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange"
                         :bottom-method="loadBottom" @bottom-status-change="handleBottomChange"
                         :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
			<mt-tab-container v-model="selected">
					 <mt-tab-container-item id="1">
					  <div v-for="n in result"  @click="details(n)" v-if="n.status==0" :key="n.title" class="rng">
					    <mt-cell class="item" >
					     <div class="rr">
					       <div class="d1">
					           <div class="t2 e" >交办协办</div>
					           <div class="e yd">{{n.status_DISPLAY}}</div>
					       </div>
					       <div class="d2">
					         <img  src="../../static/img/red.png"  class="e icon">
					           <div class="t1 e">{{n.title}}</div>
					       </div>
					       <div class="d3">
					           <div class="e tim">{{n.time}}</div>
					           <div class="e au">{{n.author}}</div>
					       </div>
					     </div>
					    </mt-cell>
					  </div>
					  </mt-tab-container-item>
			
			<mt-tab-container-item id="2">
			   <div v-for="n in result"  @click="details(n)" v-if="n.status==1" :key="n.title" class="rng2">
			     <mt-cell class="item" >
			      <div class="rr">
			        <div class="d1">
			            <div class="t2 e" >交办协办</div>
			            <div class="e yd">{{n.status_DISPLAY}}</div>
			        </div>
			        <div class="d2">
			          <img  src="../../static/img/red.png"  class="e icon">
			            <div class="t1 e">{{n.title}}</div>
			        </div>
			        <div class="d3">
			            <div class="e tim">{{n.time}}</div>
			            <div class="e au">{{n.author}}</div>
			        </div>
			      </div>
			     </mt-cell>
			   </div>
			  </mt-tab-container-item>
			
			</mt-tab-container>
			<div slot="top" class="mint-loadmore-top" style="text-align:center">
			     <span v-show="topStatus !== 'loading'" :class="{ 'is-rotate': topStatus === 'drop' }">↓</span>
			      <mt-spinner v-show="topStatus == 'loading'" color="#26a2ff"></mt-spinner>
			      <span class="mint-loadmore-text">下拉刷新</span>
			</div>
			<div slot="bottom" class="mint-loadmore-bottom">
			     <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">↑</span>
			     <mt-spinner v-show="bottomStatus == 'loading'" color="#26a2ff"></mt-spinner>
			     <span class="mint-loadmore-text">上拉刷新</span>
			</div>
							
		
</mt-loadmore>
	</div>
	
</mt-loadmore>
</div>
</template>

<script>

    export default{
        name:"FootBar",
        data(){
          return {
            result:[],
            value:"",
            selected:"1",
            list:[],
			list1:5,
			allLoaded:false,
			examplename: 'Loadmore',
			                pageNum: 1,//页码
			                InitialLoading: true,//初始加载
			                // list1: 12,//数据
			                allLoaded: false,//数据是否加载完毕
			                bottomStatus: '',//底部上拉加载状态
			                wrapperHeight: 0,//容器高度
			                topStatus: '',//顶部下拉加载状态
							topText: '',
							topPullText: '下拉刷新',
							topDropText: '释放更新',
							topLoadingText:  '加载中...',
							bottomText: '',
							bottomPullText:  '上拉刷新',
							bottomDropText:  '释放更新',
							bottomLoadingText:  '加载中...',

			
            };
      },
        mounted(){
			  let windowWidth = document.documentElement.clientWidth;//获取屏幕宽度
			            if (windowWidth >= 768) {//这里根据自己的实际情况设置容器的高度
			                this.wrapperHeight = document.documentElement.clientHeight - 105;
			            } else {
			                this.wrapperHeight = document.documentElement.clientHeight - 80;
			            }

          this.$http.get('static/js/data.json').then(res => {
			  
			  
          	this.list=res.body.result;			
						$.each(res.body.result,function(i,value){					
							var t1="2020-";
							var t2="";					   
							 if(res.body.result[i].time.length<16){
								   t2=t1.concat(res.body.result[i].time);
								  res.body.result[i].time=t2;						  
							 }
					})
            var array=this.list.sort(function(a,b){return b['time'] < a['time'] ? 1 : -1 });
            array=array.reverse();
			$.each(array,function(i,value){
					if(array[i].time.indexOf("2020-")!="-1"){
						var temp=array[i].time.slice(5);
						array[i].time=temp;
						
					}		  
				 })
      this.list=array;
            this.result=this.list;

          })
		  
		  
		  
		  
      },
	  updated(){

		  var r=this.list1;
		 $(".rng").each(function(i,value){
		                if(i>=r){
							$(this).css({
						   display:"none"
					   });
						}
		             })
		$(".rng2").each(function(i,value){
		               if(i>=r){
									$(this).css({
								   display:"none"
							   });
								}
		            })
		  
	  },
       methods: {
		   qk(){
			    this.value="";
				$(".rng").each(function(i,value){
				               if(i>=5){
											$(this).css({
										   display:"none"
									   });
										}
				            })
				$(".rng2").each(function(i,value){
				               if(i>=5){
											$(this).css({
										   display:"none"
									   });
										}
				            })
		   },
        handleClick: function() {
         this.$toast('Hello world!');
         $("#w").css({
           color:"red",
            width:"200px",
             height:"100px",
             background:"red"

            })
              },
        details:function(n){

           this.$router.push({
             path: '/Details',
             query: {
               s0:n
             }
            })

        },

		handleBottomChange() {
		                this.bottomStatus = status;
		            },
		            loadBottom() {
					
						if(this.list1>=15){
							 this.handleBottomChange("loading");
							 this.handleBottomChange("loadingEnd");//数据加载完毕 修改状态码
							 this.$refs.loadmore.onBottomLoaded();			
							return;
						}
						//console.log("loadBottom()")
		                this.handleBottomChange("loading");//上拉时 改变状态码
		                ///this.pageNum += 1;
		                setTimeout(() => {//上拉加载更多 模拟数据请求这里为了方便使用一次性定时器
		                    if (this.pageNum <= 3) {//最多下拉三次
		                        // this.list1 += 3;//上拉加载 每次数值加12
		                    } else {
		                        ///this.allLoaded = true;//模拟数据加载完毕 禁用上拉加载
		                    }
		                    this.handleBottomChange("loadingEnd");//数据加载完毕 修改状态码
		                    this.$refs.loadmore.onBottomLoaded();			
		                }, 150);
						 this.list1 += 5;//下拉刷新 数据初始化	
						var sss=this.list1;
						// console.log("this.list1"+this.list1)
						
						if(this.selected==1){
							$(".rng").each(function(i,value){
														
																	if(sss>i){
																		$(this).css({
																		display:"block"
																		});
																	}
																	
							           })
						}
						else{
							$(".rng2").each(function(i,value){
														
																	if(sss>i){
																		$(this).css({
																		display:"block"
																		});
																	}
																	
							           })
						}
						
		            },
		            handleTopChange() {
		                this.topStatus = status;
		            },
		            loadTop() {//下拉刷新 模拟数据请求这里为了方便使用一次性定时器
					
		               this.handleTopChange("loading");//下拉时 改变状态码
		                this.allLoaded = false;//下拉刷新时解除上拉加载的禁用
						this.list1 = 5;//下拉刷新 数据初始化
					   var sss=this.list1;
						//.console.log("this.list1"+this.list1)
						if(this.selected==1){
							$(".rng").each(function(i,value){
																  
																	if(i>sss-1){
																		
																		$(this).css({
																		display:"none"
																		});
																	}
																	
							           })
						}
						else{
							$(".rng2").each(function(i,value){
																  
																	if(i>sss-1){
																		
																		$(this).css({
																		display:"none"
																		});
																	}
																	
							           })
						}
						
						
		                setTimeout(() => {
		                    // this.list1 += 3;//下拉刷新 数据初始化
							
		                    this.handleTopChange("loadingEnd")//数据加载完毕 修改状态码
		                    this.$refs.loadmore.onTopLoaded();
		                }, 150);

		            },

      },
       watch:{
                           "value":function(){
                               this.result="";
                               var s=this.list;
                               var v=this.value;
                               var re=[];
                               if(this.v==" "){
                                 this.result=this.list;
                                 return;
                               }
                               $.each(s,function(i,value){
                                          if(s[i].title.indexOf(v)!=-1){
                                             re.push(s[i]);
                                          }
                                          })
                               this.result=re;
							   //当搜索框里有数据时 始终设置此样式
							   $(".mint-search-list")[0].style.display="none";

							  
                           }
                },
}
</script>

<style scoped>
/* .mint-searchbar{
  background-color: red!important;
} */
.mint-search-list{
	display: block;
}
.rr{
  margin-left: 20px;
  margin-right: 20px;
}
a{
  color: #000000;
}
a:hover{
  text-decoration: none;
}
.yd{
  float: right;
  font-size: 13px;

  color: rgb(31,214,196);
  background-color: rgb(214,250,250);
}
.t2{
 border: 1px solid rgb(106,157,229);
 font-size: 13px;
 color:rgb(106,157,229) ;
 padding-left: 3px;
 padding-right: 3px;
 border-radius: 4px;
 float: left;

}
.d2{

 margin-top: -8px;

}
.icon{

  position: relative;
  width: 10px;
  height: 10px;
  top:15px;
  left: -15px;


}
.t1{
  /* width: 95%; */
 /* padding-right: 10px; */
  position: relative;
  left: 0px;
  color: #000000;
  font-size: 19px;


}
.d1{
      overflow: auto;
      margin-top: 15px;
}
.d3{
 margin-top: 10px;
 margin-bottom: 20px;

}
.au{
  font-size: 13px;
}


.title{
  font-size: 19px;

}
.title:hover{

  text-decoration: none;
}

.tim{
  float: right;
  font-size: 13px;
}
.mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid rgb(34,184,171);
    color: rgb(34,184,171);
    margin-bottom: 3px;
}
.search{
  height: auto;
}
.page-loadmore-wrapper {
        overflow: scroll;
        z-index: 100;
    }
 
    .hot-list {
        padding: 0 8px;
    }
 
    .hot-item {
        padding: 10px 0;
    }
 
    .hot-one {
        overflow: hidden;
        border-bottom: 1px dashed #ccc;
    }
 
    .hot-one a img {
        padding-right: 10px;
    }
 
    .hot-item a img {
        width: 135px;
        height: 90px;
    }
 
    .fl {
        float: left;
    }
 
    .hot-one a h5 {
        margin-top: 2px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        margin-bottom: 6px;
        font-size: 16px;
        color: #000;
    }
 
    .hot-one a p {
        font-size: 12px;
        color: #828282;
        margin: 0 0 3px;
    }
 
    .color_e85647 {
        color: #e85647;
    }
 
    div.hot-list > div:first-child .img-box {
        overflow: hidden;
    }
 
    div.hot-list > div:first-child img {
        width: 100%;
        height: auto;
        padding-right: 0;
    }



</style>



